
body{
    background: rgb(116, 135, 149);
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    top: 50%;
    left: 50%;
    background: rgb(255, 255, 255);
    border-radius: 10px;
    min-width: 100px;
    position: absolute;
    min-height: 390px;
    transform: translate(-50%,-50%);
    border:solid  black 1px;
    gap: 20px;
}
.text{
    margin-top: 10px;
}
.btn{
    padding: 10px;
    margin-left: 10px;
    height: 50px;
    width: 90px;
    background-color: rgb(6, 6, 51)!important;
    color: #ffffff;
}
.form-control{
    width: 500px;
    /* margin-top: 20px;
    padding-top: 20px; */
}
